.messageWrapper{display:flex;width:300px}.messageWrapper *{margin:0;padding:0;box-sizing:border-box}.messageWrapper:not(:first-child){margin-top:3px}.messageWrapper .triangle{display:inline-block;border-top:rgba(0,0,0,0) solid 30px;border-bottom:rgba(0,0,0,0) solid 30px;border-right:rgba(0,0,0,0) solid 30px}.messageWrapper .textWrapper{position:relative;padding:0 10px;flex-grow:1;display:flex;flex-direction:column;justify-content:center}.messageWrapper .textWrapper .title{line-height:.8;font-size:20px}.messageWrapper .textWrapper .closeButton{position:absolute;top:0;right:5px;opacity:.5;cursor:pointer}.messageWrapper .textWrapper .closeButton:hover{opacity:1}.messageWrapper .textWrapper .closeButton::after{content:"â®¾";font-size:20px}.error>div:first-child{border-right:rgba(255,0,0,.5) solid 30px}.error>div:last-child{background-color:rgba(255,0,0,.5)}.warning>div:first-child{border-right:rgba(255,215,0,.5) solid 30px}.warning>div:last-child{background-color:rgba(255,215,0,.5)}.info>div:first-child{border-right:rgba(0,128,0,.5) solid 30px}.info>div:last-child{background-color:rgba(0,128,0,.5)}